import React, { useState } from 'react';
import { household } from './utils';

type Props = {};

export default function Household(props: Props) {
    const [house, setHouse] = useState(household());

    return (
        <div>
            <div style={{
                maxWidth: '700px',
                color: 'white',
                margin: '10px'
            }}>
                {house.map((p) => {
                    const { age, species, shape, sex, variants, job } = p;
                    return <div>{`${sex} ${species} (${shape}%${variants.length > 0 ? variants.join(',') : ''
                        }), ${age} yr${job == null ? '' : ` (${job})`}`}</div>;
                })}
            </div>
            <button onClick={() => setHouse(household())}>Generate</button>
        </div>
    );
}